(function(window) {
    'use strict';

    var unknown = '-';

    var isMobile = {
        Android: function() {
            return navigator.userAgent.match(/Android/i);
        },
        BlackBerry: function() {
            return navigator.userAgent.match(/BlackBerry/i);
        },
        iOS: function() {
            return navigator.userAgent.match(/iPhone|iPad|iPod/i);
        },
        Opera: function() {
            return navigator.userAgent.match(/Opera Mini/i);
        },
        Windows: function() {
            return navigator.userAgent.match(/IEMobile/i) || navigator.userAgent.match(/WPDesktop/i);
        },
        any: function() {
            return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows());
        }
    };

    var generate = {
        String: function(length) {
            var text = '';
            var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

            for (var i = 0; i < length; i++) {
                text += possible.charAt(Math.floor(Math.random() * possible.length));
            }

            return text;
        },
        NumberString: function(length) {
            var text = '';
            var possible = '0123456789';

            for (var i = 0; i < length; i++) {
                text += possible.charAt(Math.floor(Math.random() * possible.length));
            }

            return text;
        }
    };


    function LoopAnalytics() {
        console.log('LoopAnalytics loaded');
        return {
            Init: function(params) {
                if (this.getInitialized()) {
                    console.info('LoopAnalytics.init has already been called - this could indicate a problem');
                    return;
                }

                if (params.accessCode !== null) {
                    this.setClientID(params.accessCode);
                }

                if (params.trackerName) {
                    if (typeof params.trackerName === 'string') {
                        this.setClientTrackerName(params.trackerName);
                    }
                }

                if (params.encoding) {
                    if (typeof params.encoding === 'string') {
                        this.setEncode(params.encoding);
                    }
                }

                var requestData = {
                    'accessCode': params.accessCode,
                    'trackerName': params.trackerName,
                    'viewportSize': this.getViewPortSize(),
                    'screenResolution': this.getScreenResolution(),
                    'encoding': params.encoding,
                    'screenColors': this.getScreenColorDepth(),
                    'language': this.getLanguage(),
                    'deviceID': this.getDeviceID(),
                    'deviceName': this.getDeviceName(),
                    'deviceModel': this.getDeviceModel(),
                    'deviceBrand': this.getDeviceBrand(),
                    'osName': this.getOperatingSystem(),
                    'osVersion': this.getOperatingSystemVersion(),
                    'appName': this.getAppName(),
                    'appID': this.getAppID(),
                    'appVersion': this.getAppVersion()
                };

                var xhr = new XMLHttpRequest();
                xhr.open("POST", "http://localhost:3001/analytics/1.0/create", false);
                xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
                xhr.send(JSON.stringify(requestData));

                if (xhr.status === 200 && xhr.statusText === 'OK') {
                    var resp = JSON.parse(xhr.response);
                    this.setSessionID(resp.SessionID);
                }
            },
            getInitialized: function() {
                return Cookies.get('LoopAnalytics_Session') ? true : false;
            },
            setClientID: function(accessCode) {
                Cookies.set('LoopAnalytics_accessCode', accessCode);
            },
            setClientTrackerName: function(trackerName) {
                Cookies.set('LoopAnalytics_trackerName', trackerName);
            },
            setEncode: function(code) {
                Cookies.set('LoopAnalytics_encode', code);
            },
            setSessionID: function(session) {
                Cookies.set('LoopAnalytics_Session', session);
            },
            getSessionID: function() {
                return Cookies.get('LoopAnalytics_Session') ? Cookies.get('LoopAnalytics_Session') : null;
            },
            getViewPortSize: function() {
                var viewPortWidth;
                var viewPortHeight;

                // the more standards compliant browsers (mozilla/netscape/opera/IE7) use window.innerWidth and window.innerHeight
                if (typeof window.innerWidth != 'undefined') {
                    viewPortWidth = window.innerWidth,
                        viewPortHeight = window.innerHeight;
                }

                // IE6 in standards compliant mode (i.e. with a valid doctype as the first line in the document)
                else if (typeof document.documentElement != 'undefined' && typeof document.documentElement.clientWidth !=
                    'undefined' && document.documentElement.clientWidth !== 0) {
                    viewPortWidth = document.documentElement.clientWidth,
                        viewPortHeight = document.documentElement.clientHeight;
                }

                // older versions of IE
                else {
                    viewPortWidth = document.getElementsByTagName('body')[0].clientWidth,
                        viewPortHeight = document.getElementsByTagName('body')[0].clientHeight;
                }
                return viewPortWidth + 'x' + viewPortHeight;
            },
            getScreenResolution: function() {
                var screenHeight;
                var screenWidth;

                if (typeof window.screen.availHeight != 'undefined') {
                    screenWidth = window.screen.availWidth,
                        screenHeight = window.screen.availHeight;
                }

                return screenWidth + 'x' + screenHeight;
            },
            getScreenColorDepth: function() {
                if (typeof window.screen.colorDepth != 'undefined') {
                    return window.screen.colorDepth + '-bits';
                }
            },
            getLanguage: function() {
                var userLang = navigator.language || navigator.userLanguage;
                return userLang;
            },
            getOperatingSystem: function() {
                var nAgt = navigator.userAgent;
                var os = unknown;

                var clientStrings = [{
                    s: 'Windows 10',
                    r: /(Windows 10.0|Windows NT 10.0)/
                }, {
                    s: 'Windows 8.1',
                    r: /(Windows 8.1|Windows NT 6.3)/
                }, {
                    s: 'Windows 8',
                    r: /(Windows 8|Windows NT 6.2)/
                }, {
                    s: 'Windows 7',
                    r: /(Windows 7|Windows NT 6.1)/
                }, {
                    s: 'Windows Vista',
                    r: /Windows NT 6.0/
                }, {
                    s: 'Windows Server 2003',
                    r: /Windows NT 5.2/
                }, {
                    s: 'Windows XP',
                    r: /(Windows NT 5.1|Windows XP)/
                }, {
                    s: 'Windows 2000',
                    r: /(Windows NT 5.0|Windows 2000)/
                }, {
                    s: 'Windows ME',
                    r: /(Win 9x 4.90|Windows ME)/
                }, {
                    s: 'Windows 98',
                    r: /(Windows 98|Win98)/
                }, {
                    s: 'Windows 95',
                    r: /(Windows 95|Win95|Windows_95)/
                }, {
                    s: 'Windows NT 4.0',
                    r: /(Windows NT 4.0|WinNT4.0|WinNT|Windows NT)/
                }, {
                    s: 'Windows CE',
                    r: /Windows CE/
                }, {
                    s: 'Windows 3.11',
                    r: /Win16/
                }, {
                    s: 'Android',
                    r: /Android/
                }, {
                    s: 'Open BSD',
                    r: /OpenBSD/
                }, {
                    s: 'Sun OS',
                    r: /SunOS/
                }, {
                    s: 'Linux',
                    r: /(Linux|X11)/
                }, {
                    s: 'iOS',
                    r: /(iPhone|iPad|iPod)/
                }, {
                    s: 'Mac OS X',
                    r: /Mac OS X/
                }, {
                    s: 'Mac OS',
                    r: /(MacPPC|MacIntel|Mac_PowerPC|Macintosh)/
                }, {
                    s: 'QNX',
                    r: /QNX/
                }, {
                    s: 'UNIX',
                    r: /UNIX/
                }, {
                    s: 'BeOS',
                    r: /BeOS/
                }, {
                    s: 'OS/2',
                    r: /OS\/2/
                }, {
                    s: 'Search Bot',
                    r: /(nuhk|Googlebot|Yammybot|Openbot|Slurp|MSNBot|Ask Jeeves\/Teoma|ia_archiver)/
                }];
                for (var id in clientStrings) {
                    var cs = clientStrings[id];
                    if (cs.r.test(nAgt)) {
                        os = cs.s;
                        break;
                    }
                }
                return os;
            },
            getOperatingSystemVersion: function() {
                var nAgt = navigator.userAgent;
                var nVer = navigator.appVersion;
                var os = this.getOperatingSystem();
                var osVersion = unknown;

                switch (os) {
                    case 'Mac OS X':
                        osVersion = /Mac OS X (10[\.\_\d]+)/.exec(nAgt)[1];
                        console.log('osVersion: ', osVersion);
                        break;
                    case 'Android':
                        osVersion = /Android ([\.\_\d]+)/.exec(nAgt)[1];
                        break;
                    case 'iOS':
                        osVersion = /OS (\d+)_(\d+)_?(\d+)?/.exec(nVer);
                        osVersion = osVersion[1] + '.' + osVersion[2] + '.' + (osVersion[3] | 0);
                        break;
                }
                return osVersion;
            },
            getDeviceID: function() {
                if (isMobile.any()) {

                } else {
                    return generate.NumberString(8);
                }
            },
            getDeviceName: function() {
                if (isMobile.any()) {

                } else {
                    return navigator.platform;
                }
            },
            getDeviceModel: function() {
                if (isMobile.any()) {

                } else {
                    return (this.getOperatingSystem() + ' ' + this.getOperatingSystemVersion());
                }
            },
            getDeviceBrand: function() {
                if (isMobile.any()) {

                } else {
                    return this.getOperatingSystem();
                }
            },
            getAppID: function() {
                return navigator.productSub + '-' + generate.NumberString(8);
            },
            getAppName: function() {
                return navigator.appName;
            },
            getAppVersion: function() {
                return navigator.appVersion;
            }
        };
    }

    if (typeof(LoopAnalytics) === 'function') {
        window.LoopAnalytics = new LoopAnalytics();
    } else if (!window.LoopAnalytics) {
        window.LoopAnalytics = new LoopAnalytics();
    } else if (typeof exports === 'object') {
        module.exports = LoopAnalytics;
    } else {
        console.log('LoopAnalytics already defined.');
    }

})(window);
